.hide {
    display: none;
}

.work {
    overflow: hidden;
    transition: transform .2s linear;
    cursor: pointer;

    &:hover {
        transform: translateY(-3px);

    }
}

.work__image {
    display: block;
    max-width: 100%;
    height: auto;
    overflow: hidden;
    position: relative;
}


.work__content {
    padding: .4rem 0;
}

.work__cat {
    margin-bottom: 0.5rem;
    color: $gray;
}

.work__title {
    text-transform: uppercase;
    color: $black;
    font-size: 1.2rem;
    font-weight: 700;
    margin-bottom: .4rem;
    display: flex;
    justify-content: space-between;

}

.work__subtitle {
    color: $gray;
    font-size: .9rem;
    font-weight: 700;

}