.modal__contact {
    // min-width: 700px;
}

.contact {
    display: flex;
    // max-width: 700px;
}

.contact__title {
    font-size: 2rem;
    font-weight: 800;
    color: $black;
    margin-bottom: 0.5rem;
}

.contact__subtitle {
    display: inline-block;
    font-size: 1rem;
    font-weight: 400;
    color: $gray;
    margin-bottom: 2rem;
    min-width: 440px;
    max-width: 500px;
}

.contact__left {
    width: 100%;
    padding: 2rem;
}

.contact__info {
    margin: 2rem 0 3rem;
    padding: 0;
    list-style: none;
    font-size: 1rem;
    color: $gray;
    font-weight: 400;
}

.contact__info-item {
    display: flex;
    align-items: center;
    // margin-bottom: .75rem;
}

.contact__info-item:last-child {
    margin-bottom: 0;
}

.contact__info-icon {
    margin-right: .75rem;
}

.form__group {
    margin-bottom: 1rem;
}

.form__label {
    display: inline-block;
    margin-bottom: .6rem;
    font-size: .8rem;
    color: #2b2a2c;
    font-weight: 700
}

.form__input,
.form__textarea {
    display: block;
    width: 100%;
    padding: 1.2rem 0.8rem;
    border: 1px solid rgba(112, 112, 112, 0.3);
    border-radius: 0.2rem;
    font-family: inherit;
    font-size: 1rem;
    line-height: 1.2;
    font-weight: 400;
    opacity: 0.5;
}

.form__textarea {
    min-height: 180px;
}

.form__input:focus,
.form__textarea:focus {
    outline: none;
    border-color: #575757;
}

.form__input::placeholder,
.form__textarea::placeholder {
    color: #2b2a2c
}

.form__textarea {
    height: 100px;
    resize: none;
}

.text-center {
    text-align: center;
}

.text-left {
    text-align: left;
    margin-top: 1rem;
}

.btn__contact {
    cursor: pointer;
    letter-spacing: 1px;
    font-size: 1rem;
    font-weight: 400;
    // padding: 0.8rem 2rem;
    border: 1px solid $black;
    border-radius: 4px;
    transition: background-color .3s ease-in-out,
        color .3s ease-in-out;
    width: 440px;
    height: 54px;
    text-align: center;
    align-items: center;
    display: block;
    background-color: $black;
    color: $white;
    font-family: inherit;

    &:hover {
        background-color: $white;
        color: $black;
    }
}

.number {
    text-decoration: none;
    color: #333333;

    &.number:hover {
        color: #990100;
    }
}