.about {
    margin: 8rem 0;
}

.about__inner {
    display: flex;
    flex-wrap: wrap;
    max-width: 1090px;
}

.about__photo,
.about__content {
    width: 50%;

    @media screen and (max-width: 800px) {
        width: 100%;
        flex-wrap: wrap;
        ;
    }
}

.about__content {
    padding-left: 2rem;

    @media screen and (max-width: 1000px) {
        padding-left: 1rem;
    }
}

.about__bigtitle {
    letter-spacing: 0px;
    font-size: 4.2rem;
    // text-transform: uppercase;
    color: $black;
    line-height: 1.1;
    margin-bottom: 1rem;
    font-weight: 700;

    @media screen and (max-width: 800px) {
        margin-top: 1rem;
        margin-bottom: 1rem;
    }
}

.about__text {
    padding: 0 .4rem;
    font-size: 1rem;
    color: $gray;
    line-height: 1.5;
    margin-bottom: 1.8rem;

}

.social--about {
    padding: 0 .4rem;
    margin: 0;

}

.nav__link--btn--about {
    margin: 0 auto;
    margin: 0 .4rem;
    align-items: center;
}



