.intro {
    height: 100vh;

}

#birds {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
    z-index: -2;
}

.intro__inner {

    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;

    @media screen and (max-width: 1000px) {
        padding-top: 10rem;
        padding-bottom: 8rem;
        line-height: 1.2;
    }

    @media screen and (max-width: 600px) {
        line-height: 1.2;
        text-align: center;
        align-items: center;
        padding-top: 16rem;
    }
}

.intro__content {
    width: 50%;
    padding-top: 12rem;

    @media screen and (max-width: 600px) {
        width: 100%;
    }
}

.intro__title {
    font-weight: 700;
    font-size: 8rem;
    color: $black;
    margin-top: 2rem;
    color: $black;
    opacity: 0.8;
    line-height: 1;


    @media screen and (max-width: 600px) {
        font-size: 3rem;
        line-height: 1;
        margin-bottom: 0.5rem;
    }
}

.intro__subtitle {
    font-weight: 600;
    font-size: 1.4rem;
    margin-bottom: 24px;
    color: $gray;

    @media screen and (max-width: 600px) {
        font-size: 1rem;
        line-height: 1.2;


    }
}

.disk {
    position: relative;
    background: #4a9266;
    width: 30%;
    padding: 15% 0;
    border-radius: 50%;
    /* адаптивный круг */
    margin: 10% auto;
    font: 4.5vmin monospace;
}

.intro__media {
    width: 50%;
    bottom: 0;
    right: 0;
    z-index: 1;

    @media screen and (max-width: 1000px) {
        margin-right: 6rem;
    }

    @media screen and (max-width: 600px) {
        // margin-top: 6rem;
        width: 100%;
        margin-bottom: 6rem;
    }

}