.modal-work {
    display: flex;
}

.modal-work__preview {
    width: 65%;
    position: relative;
    overflow: hidden;
}


.modal-work__content {
    width: 35%;
    padding: 3.4rem 4rem 1rem 1.73rem;
    position: relative;
}

.modal-work__photo {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    transform: translate3d(-50%, -50%, 0);
}

.modal-work__title {
    font-size: 2rem;
    font-weight: 700;
    color: $black;
    line-height: 1.2;
    margin-bottom: 1rem;
}

.modal-work__info {
    font-size: 1rem;
    font-weight: 400;
    color: $gray;
    margin-bottom: 1rem;

}

.modal-work__info--divider {
    // margin: 0 0.5rem;
}

.modal-work__client {
    font-size: 1rem;
    font-weight: 400;
    color: $black;
}

.modal-work__text {
    font-size: 1rem;
    font-weight: 400;
    color: $gray;
    line-height: 1.6;
    margin-bottom: 8.66rem;
}

.modal-work__footer {
    display: flex;
    justify-content: space-between;
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    z-index: 1;
    padding: 0 1.8rem 1.8rem 1rem;
}

.modal-work__btn {
    font-size: 0.94rem;
    color: $gray;
    border: none;
    cursor: pointer;
    font-family: inherit;
    padding: 0;
    background-color: #fff;
    display: flex;
    align-items: center;
    outline: none;

    &:hover {
        color: $red;
    }
}

.modal-work__btn--prev {
    margin-right: 0.2rem;
}

.modal-work__btn--icon {
    &:hover {
        fill: $red;
    }
}

.modal-work__btn--next {
    margin-left: 0.3rem;
}

.modal__close {
    display: flex;
    align-items: center;
    fill: $gray;

    &:hover {
        fill: $red;
        stroke: $red;
    }
}