.offer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 600px;
}

.offer__content {
    width: 50%;
    display: inline-block;
    position: absolute;
    top: 0;
}

.offer__media {
    width: 50%;
    display: inline-block;
    position: absolute;
    padding-left: 50%;
}

.offer__title {
    font-size: 4.2rem;
    line-height: 1.5;
    font-weight: 700;
    margin: 0;
    padding: 0;
    margin-bottom: 3rem;
    letter-spacing: 0px;

    @media screen and (max-width: 800px) {
        font-size: 3rem;
        font-weight: 700;
        margin-bottom: 1.5rem;
    }
}

.offer__text {
    z-index: 10;
    font-size: 2.2rem;
    font-weight: 400;
    color: $gray;
    text-decoration: none;
    padding-bottom: 1rem;

    @media screen and (max-width: 800px) {
        font-size: 1.4rem;
        font-weight: 500;
    }
}

.offer__subtitle {
    font-size: 1rem;
    font-weight: 400;
    color: $gray;
    text-decoration: none;
    margin: 1rem 0;

    &:hover {
        color: $red;
    }
}

.offer__media {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;

    &:before {
        content: '';
        top: 0;
        left: 0;
        z-index: 5;
        width: 100%;
    }
}