.portfolio {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1rem;
}

.portfolio__col {
    margin-top: 1rem;
    flex-basis: 33.33333%;

    padding: 1rem;

    @media screen and (max-width: 800px) {
        flex-basis: 50%;
    }

    @media screen and (max-width: 500px) {
        flex-basis: 100%;
    }
}

.project__link {
    text-decoration: none;
    font-weight: 600;
    color: $black;

    &:hover {
        color: $red;
    }
}