.modal {
    display: none;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    background-color: $black;
    padding: 1rem;
    overflow: scroll;

    &.show {
        display: flex;
    }
}

.modal__dialog {
    width: 100%;
    max-width: 1200px;
    position: relative;
    margin: auto;
    background-color: #fff;

    transform: scale(0);
    transition: transform .2s linear;

    &--sm {
        max-width: 500px;
    }
}

.modal__close {
    position: absolute;
    top: 1.8rem;
    right: 1.8rem;
    z-index: 1;
    border: 0;
    background: none;
    cursor: pointer;
    outline: none;
    transition: transform .2s linear;
    fill: red;

    &:hover {
        transform: rotate(90deg);
        fill: $red;
    }

}

.modal__content {
    padding: 5rem 1rem;
}

.modal__title {
    font-size: 2rem;
    font-weight: 700;
    color: $black;

}

.modal__subtitle {
    font-size: 1.06rem;
    color: $black;
    font-weight: 400;
}