.footer {
    padding: 1.35rem 0;


}

.footer__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footer__credits {
    display: flex;
    align-items: center;
}

.footer__copyright {
    margin-right: 1rem;
    padding-right: 8rem;
    font-size: .94rem;
    color: $black;
    border-right: 1px solid fade($gray, 35%);
}

.footer__social {
    display: flex;
    align-items: center;

    a {
        text-decoration: none;
        margin-right: 1rem;
    }
}

.footer__nav {
    display: flex;
    align-items: center;
}

.footer__nav--link {
    margin-left: 1.5rem;
    color: $black;
    font-size: 0.94rem;
    text-decoration: none;
    position: relative;

    &:hover:not(.footer__nav--link--btn) {
        &:hover {
            color: $red;

        }
    }

    &--btn {
        letter-spacing: 0.4px;
        font-size: .94rem;
        font-weight: 500;
        padding: 0.85rem 2rem;
        border: 1px solid $black;
        border-radius: 4px;
        transition: background-color .3s ease-in-out,
            color .3s ease-in-out;
        min-width: 160px;
        height: 48px;
        text-align: center;
        align-items: center;
        display: inline-block;
        background-color: $black;
        color: $white;

        &:hover {
            background-color: $white;
            color: $black;
        }
    }
}

.footer__nav--link--btn {
    @media screen and (max-width: 880px) {
        letter-spacing: 0.1px;
        font-size: 0.8rem;
        font-weight: 500;
        padding: 0.7rem 1rem;
        border: 1px solid $black;
        border-radius: 2px;
        transition: background-color .3s ease-in-out,
            color .3s ease-in-out;
        width: 120px;
        height: 40px;
        text-align: center;
        align-items: center;
        display: inline-block;
        background-color: $black;
        color: $white;
    }
}