.works {
    margin: 2.8rem 0;
}

.works__info {
    display: flex;
    align-items: baseline;
    justify-content: space-between;

    @media screen and (max-width: 800px) {
        display: block;
    }
}

.works__info--title {
    font-size:
        4.2rem;
    font-weight: 700;
    margin-bottom: 1rem;

    @media screen and (max-width: 800px) {
        font-size: 3rem;
        font-weight: 700;
        margin-bottom: 1rem;
    }

}

// .works__info--link {
//     float: right;
//     text-decoration: none;
//     color: $gray;

// }

.works__nav {
    display: flex;
    text-align: right;
    margin-bottom: 1.4rem;

    @media screen and (max-width: 600px) {
        display: flex;
        margin-bottom: 0;
        flex-wrap: wrap;
        text-align: left;

    }
}

.works__nav-link:last-child {
    margin-right: 0 !important;
}

.works__nav-link {
    font-size: 1rem;
    text-decoration: none;
    color: $black;
    cursor: pointer;
    position: relative;
    margin: 0 1.5rem;

    @media screen and (max-width: 999px) {
        margin-right: 1rem;
        margin-left: 0;
        margin-bottom: 0.6rem;
    }

    @media screen and (max-width: 600px) {
        margin-right: 1rem;
        margin-left: 0;
        margin-bottom: 0.6rem;
    }
}


