.decoration-text {
    margin-bottom: 10rem;

    @media screen and (max-width: 600px) {
        margin: 6rem 0;
    }
}

.decoration-text__title {
    max-width: 1000px;
    font-size: 2.2rem;
    line-height: 1.5;
    font-weight: 600;
    display: flex;
    margin: 0;
    padding: 0;
    margin-bottom: 1.2rem;
}

.decoration-text__info--subtitle {
    font-size: 1rem;
    font-weight: 400;
    color: $gray;
    text-decoration: none;
}