.nav {
    display: flex;
    height: 100%;
    align-items: center;

    @media screen and (max-width: 800px) {
        display: none;
    }
}

/*Burger*/

.menu-icon-wrapper {
    /*   background: red; */
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    @media screen and (min-width: 801px) {
        display: none;
    }
}

.menu-icon {
    position: relative;
    width: 24px;
    height: 3px;
    background-color: $black;
}

.menu-icon::before {
    position: absolute;
    left: 0;
    top: -8px;
    content: '';
    width: 24px;
    height: 3px;
    background-color: $black;
    transition: transform 0.2s ease-in, top 0.2s linear 0.2s;

}

.menu-icon::after {
    position: absolute;
    left: 0;
    top: 8px;
    content: '';
    width: 24px;
    height: 3px;
    background-color: $black;
    transition: transform 0.2s ease-in, top 0.2s linear 0.2s;

}

.menu-icon.menu-icon-active {
    background-color: transparent;
}

.menu-icon.menu-icon-active::before {
    transform: rotate(45deg);
    top: 0;
    transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
    background-color: $red;
}


.menu-icon.menu-icon-active::after {
    transform: rotate(-45deg);
    top: 0;
    transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
    background-color: $red;
}


.nav__link {
    font-size: 1rem;
    color: $black;
    font-weight: 500;
    text-decoration: none;
    // border: 2px solid $black;
    // border-radius: 4px;
    cursor: pointer;
    margin-left: 2rem;
    position: relative;


    &:after {
        content: "";
        display: block;
        width: 0px;
        height: 1px;
        background-color: $gray;
        position: absolute;
        bottom: -8px;
        left: 0;
        z-index: 1;
        width: 0%;
        left: 50%;
        -webkit-transition: width .3s ease-in-out;
        -moz--transition: width .3s ease-in-out;
        transition: width .3s ease-in-out;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        transform: translateX(-50%);
    }

    &:hover:not(.nav__link--btn) {
        &:after {
            display: block;
            width: 100%;
        }
    }

    &:active:not(.nav__link--btn) {
        &:after {
            content: "";
            display: block;
            height: 2px;
            background-color: $gray;
            position: absolute;
            bottom: -8px;
            left: 0;
            z-index: 1;
            width: 100%;
            left: 50%;
        }
    }

    &--btn {
        letter-spacing: 0.4px;
        font-size: 1rem;
        font-weight: 500;
        padding: 0.8rem 2rem;
        border: 1px solid $black;
        border-radius: 4px;
        transition: background-color .3s ease-in-out,
            color .3s ease-in-out;
        min-width: 160px;
        height: 48px;
        text-align: center;
        align-items: center;
        display: inline-block;
        background-color: $black;
        color: $white;

        @media screen and (max-width: 500px) {
            letter-spacing: 0.1px;
            font-size: 0.8rem;
            font-weight: 500;
            padding: 0.7rem 1rem;
            border: 1px solid $black;
            border-radius: 2px;
            transition: background-color .3s ease-in-out,
                color .3s ease-in-out;
            min-width: 80px;
            height: 40px;
            text-align: center;
            align-items: center;
            display: inline-block;
            background-color: $black;
            color: $white;
        }

        &:hover {
            background-color: $white;
            color: $black;
        }

        +.btn {
            margin-left: 1rem;
        }
    }
}