.news {
    margin-bottom: 10rem;
}

.news__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.news__link {
    float: right;
    text-decoration: none;
    color: $gray;

    &:hover {
        color: $red;
    }

}

.news__title {
    letter-spacing: 0px;
    font-size: 4.2rem;
    // text-transform: uppercase;
    color: fade($black, 15%);
    line-height: 1.1;
    margin-bottom: 3rem;
    font-weight: 700;

    @media screen and (max-width: 800px) {
        font-size: 3rem;
        font-weight: 700;
        margin-bottom: 1rem;
    }
}

.news__subtitle {
    padding: 0 .4rem;
    font-size: 1rem;
    color: $gray;
    line-height: 1.5;
    margin-bottom: 1.8rem;
}