.social {
    margin-bottom: 2.8rem;
    display: flex;

}

.social__link {
    text-decoration: none;
    margin-right: 10px;
    transition: transform .2s linear;
    text-decoration: none;
    margin-right: 1rem;
    color: $gray;

    &:hover {
        color: $red;

    }
}

.social__link--btn {
    margin-top: 32px;
    display: inline-block;
    margin: 0 auto;
}