* {
  box-sizing: border-box;
}

html {
  font-size: 15px;
}

body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  font-size: 1rem;
  // background-color: rgba(245, 245, 245, 1);
  //   background-color: #f5f5f5;
  background-color: #f5f6f8;

  &.no-scroll {
    overflow: hidden;
  }
}

