.articles {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1rem;
}

.articles__link {
    text-decoration: none;
    color: $gray;
}

.articles__col {
    width: 50%;
    padding: 0 1rem;

    @media screen and (max-width: 800px) {
        width: 100%;
        flex-wrap: wrap;
        padding: 2rem 1rem;
        align-items: center;
        display: flex;
        justify-content: center;
        ;
    }
}

.articles__header {
    position: relative;
    margin-bottom: 1.35rem;
}

.articles__date {
    line-height: 2.1;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 77px;
    height: 77px;
    background-color: $black;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    color: $white;
    font-size: 1rem;
}

.articles__photo {
    display: block;
    max-width: 100%;
    height: auto;
}

.articles__title {
    font-size: 2rem;
    font-weight: 600;
    line-height: 1.2;
    color: $black;
    margin-bottom: 1rem;
}

.articles__info {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.4;
    color: $gray;
}